<template>
  <div class="swipe">
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="item in swipeList" :key="item.id">
        <img :src="$pre +item.img" alt />
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
import { reqBanner } from "../../../utils/http";
export default {
  data() {
    return {
      swipeList: []
    };
  },
  mounted() {
    reqBanner().then(res => {
      this.swipeList = res.data.list;
    });
  }
};
</script>

<style scoped lang="stylus">
@import '../../../stylus/index.styl';

img {
  width 100vw
  height: $banner
}
</style>


