<template>
  <div class="home">
    <div class="swipe">
      <v-swipe></v-swipe>
    </div>
    <div class="qiang">
      <ul>
        <li v-for="item in nav" :key="item.img">
          <div class="pic"><img :src="item.img" alt=""></div>
          <h5>{{item.name}}</h5>
        </li>
      </ul>
    </div>
    <v-goods></v-goods>
  </div>
</template>

<script>
import vSwipe from "./banner/banner.vue"
import vGoods from "./goods/goods.vue"
import qianggou from "../../assets/img/img/home/1.jpg"
import buy from "../../assets/img/img/home/buy.png"
import contact from "../../assets/img/img/home/contact.png"
import jifen from "../../assets/img/img/home/jifen.png"
export default {
  mounted(){
    // reqGetseckill().then(res=>{

    // }),
    // 获取商品信息（首页）
  },
  data(){
    return {
      nav:[
        {
          name:"限时抢购",img:qianggou
        },
        {
          name:"限时抢购",img:buy
        },
        {
          name:"限时抢购",img:contact
        },
        {
          name:"积分商城",img:jifen
        }
      ]
     
    }
  },

  components:{
    vSwipe,
    vGoods
  }
}
</script>

<style scoped lang="stylus">
@import '../../stylus/index.styl';
.swipe{
  height $banner
}
ul{
   display flex
   justify-content space-around  
   margin-top .3rem
   text-align center
   h5{
     margin-top .2rem
   }
}
.pic{
  width 1.2rem
  height 1.2rem
  img{
    height 100%
    width 100%
  }
}
</style>


