<template>
  <div>
    <div class="goods" v-for="item in goods" :key="item.id" @click="detail(item.id)">
      <div class="img">
        <img :src="$pre+item.img" alt />
      </div>
      <div class="right">
        <h3>{{item.goodsname}}</h3>
        <span>&yen;{{item.price}}</span>
        <br>    
        <el-button class="button" type="primary">点击抢购</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { reqGetindexgoods } from "../../../utils/http";
export default {
    methods:{
        detail(id){
            this.$router.push("/detail?id="+id)
        }
    },
  data() {
    return {
      goods: []
    };
  },
  mounted() {
    reqGetindexgoods().then(res => {
      this.goods = res.data.list[0].content;
      console.log(this.goods);
    });
  }
};
</script>

<style scoped lang="stylus">
@import '../../../stylus/index.styl';

.goods {
  display: flex;
  margin-top $h1

  .img {
    height: $homeimg;
    width: $homeimg;
    margin-right $h1

    img {
      width: 100%;
      height: 100%;
    }
  }
  .right{
      h3{
          line-height $h6
          font-size $h2
      }
      span{
          color $red
          font-size $h2
      }
  }
}
</style>


